<template>
    <div class="login-warp">
        <div class="login-logo"><img src="../../dist/image/login.png"></div>
        <div class="login-content-icon">
            <img src="../../dist/image/icon.png" class="login-title">
        </div>
        <div class="login-content-user">
            <div class="login-content-user-name">
                <i class="iconfont icon-zhanghao login-input-icon"></i>
                <input type="text" placeholder="学工号" />
            </div>
            <div class="login-content-user-passwd">
                <i class="iconfont icon-mima login-input-icon"></i>
                <input type="password" placeholder="密码" />
            </div>
            <div class="login-user-btn" @click="login()">登录</div>
        </div>
        <div class="weui-footer login-footer" >
            <p class="weui-footer__text">Copyright &copy; 2017 西北农林科技大学体育部</p>
        </div>
    </div>
</template>

<script>
import tabbar from "../public/tabbar";
export default {
    methods:{
        login(){
            this.$router.push('/home')
        }
    }
};
</script>

<style>
.login-warp {
  width: 100%;
  height: 100%;
  background: #f8f8f8;
}

.login-logo {
  padding: 30px 20px;
}

.login-content-icon {
  height: 80px;
  width: 100%;
}

.login-title {
  margin: 0px auto;
  display: block;
  width: 118px;
  height: 72px;
}

.login-content-user {
  margin-top: 20px;
  padding: 0px 20px;
  height: 100px;
  width: 89%;
}

.login-content-user-name {
  width: 100%;
  background: #ffffff;
  height: 48px;
  border-bottom: 1px solid #cccccc;
}

.login-content-user-passwd {
  width: 100%;
  background: #ffffff;
  height: 48px;
}

.login-content-user input {
  height: 40px;
  outline: none;
  float: right;
  width: 86%;
  font-size: 16px;
  color: #333;
}

input::-webkit-input-placeholder {
  color: #a9a9a9;
  font-size: 14px;
}

.login-input-icon {
  font-size: 20px;
  padding: 0px 10px;
  line-height: 50px;
  color: #bebebe;
}

.login-user-btn {
  padding: 10px 40px;
  font-size: 16px;
  background: #259b24;
  color: #ffffff;
  margin-top: 20px;
  text-align: center;
  letter-spacing: 4px;
}
.login-footer {
  position: absolute;
  bottom: 30px;
  width: 100%;
}
</style>




