import Login from './login/index'
import Home from './home/index'
import Order from './order/index'
import Us from './us/index'
import Soccer from './soccer/index'
import Gymnastics from './gymnastics/index'
import PaySuccess from './soccer/pay_success'
import Time from './public/select_time'

const routers = [{
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    component: Login,
  }, {
    path: '/home',
    component: Home,
  }, {
    path: '/order',
    component: Order
  },
  {
    path: '/us',
    component: Us
  },
  {
    path: '/soccer',
    component: Soccer
  },
  {
    path: '/gymnastics',
    component: Gymnastics
  },
  {
    path: '/pay_success',
    component: PaySuccess
  },
  {
    path: '/time/:stat',
    component: Time
  }
]

export default routers


