l=\"startTime\">\r\n            </div>\r\n            <div class=\"time-select-every\">\r\n                <p>选择结束时间</p>\r\n                <input type=\"text\" placeholder=\"请选择日期\" class=\"select-date-input\" v-model=\"endTime\"  @click=\"getEndTime\">\r\n            </div>\r\n            <div class=\"time-select-every\">\r\n                <p class=\"active-time\">{{this.checkTime}}</p>\r\n            </div>\r\n        </div>\r\n        <div class=\"weui-btn weui-btn_primary select-place-btn\" @click=\"selectPlace\">去选场地</div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  data() {\r\n    return {\r\n      checkTime: \"\", // 当前选中的时间\r\n      currentDate: \"\",\r\n      startTime: \"14:00\",\r\n      endTime: \"15:00\",\r\n      year: \"\",\r\n      mounth: \"\",\r\n      day: \"\",\r\n      dateArr: [\r\n        {\r\n          label: \"14:00\",\r\n          value: 0\r\n        },\r\n        {\r\n          label: \"14:30\",\r\n          value: 1\r\n        },\r\n        {\r\n          label: \"15:00\",\r\n          value: 3\r\n        },\r\n        {\r\n          label: \"15:30\",\r\n          value: 4\r\n        },\r\n        {\r\n          label: \"16:00\",\r\n          value: 5\r\n        },\r\n        {\r\n          label: \"16:30\",\r\n          value: 6\r\n        },\r\n        {\r\n          label: \"17:00\",\r\n          value: 7\r\n        },\r\n        {\r\n          label: \"17:30\",\r\n          value: 8\r\n        },\r\n        {\r\n          label: \"18:00\",\r\n          value: 9\r\n        },\r\n        {\r\n          label: \"18:30\",\r\n          value: 10\r\n        },\r\n        {\r\n          label: \"19:00\",\r\n          value: 11\r\n        },\r\n        {\r\n          label: \"19:30\",\r\n          value: 12\r\n        },\r\n        {\r\n          label: \"20:00\",\r\n          value: 13\r\n        },\r\n        {\r\n          label: \"20:30\",\r\n          value: 14\r\n        },\r\n        {\r\n          label: \"21:00\",\r\n          value: 15\r\n        },\r\n        {\r\n          label: \"21:30\",\r\n          value: 16\r\n        }\r\n      ]\r\n    };\r\n  },\r\n  mounted() {\r\n    console.log(this.$route.params)\r\n    let myDate = new Date();\r\n    this.year = myDate.getFullYear();\r\n    this.mounth = myDate.getMonth() + 1;\r\n    this.day = myDate.getDate();\r\n    this.currentDate = this.year + \"/\" + this.mounth + \"/\" + this.day;\r\n    this.checkTime =\r\n      this.currentDate + \" \" + this.startTime + \"-\" + this.endTime;\r\n  },\r\n  methods: {\r\n    getDate() {\r\n      weui.datePicker({\r\n        start: new Date(), // 从今天开始\r\n        end: 2030,\r\n        defaultValue: [this.year, this.mounth, this.day],\r\n        onConfirm: result => {\r\n          this.currentDate =\r\n            result[0].value + \"/\" + result[1].value + \"/\" + result[2].value;\r\n          this.checkTime =\r\n            this.currentDate + \" \" + this.startTime + \"-\" + this.endTime;\r\n        },\r\n        id: \"datePicker\"\r\n      });\r\n    },\r\n    getStartTime() {\r\n      weui.picker(this.dateArr, {\r\n        className: \"custom-classname\",\r\n        container: \"body\",\r\n        defaultValue: [0],\r\n        onConfirm: result => {\r\n          this.startTime = result[0].label;\r\n          this.checkTime =\r\n            this.currentDate + \" \" + this.startTime + \"-\" + this.endTime;\r\n        },\r\n        id: \"singleLinePicker\"\r\n      });\r\n    },\r\n    getEndTime() {\r\n      weui.picker(this.dateArr, {\r\n        className: \"custom-classname\",\r\n        container: \"body\",\r\n        defaultValue: [0],\r\n        onConfirm: result => {\r\n          this.endTime = result[0].label;\r\n          this.checkTime =\r\n            this.currentDate + \" \" + this.startTime + \"-\" + this.endTime;\r\n        },\r\n        id: \"singleLinePicker\"\r\n      });\r\n    },\r\n    back() {\r\n      this.$router.back();\r\n    },\r\n    selectPlace(){\r\n      if(this.$route.params.stat === \"0\") {\r\n        this.$router.push(\"/soccer\")\r\n      }else{\r\n         this.$router.push(\"/gymnastics\")\r\n      }\r\n    }\r\n  }\r\n};\r\n</script>\r\n\r\n<style>\r\n.time-info {\r\n  margin-top: 60px;\r\n  font-size: 14px;\r\n  padding: 20px 10px 20px 50px;\r\n  color: #888;\r\n}\r\n\r\n.icon-time-info {\r\n  padding-right: 10px;\r\n}\r\n\r\n.time-select-container {\r\n  width: 100%;\r\n  height: 74%;\r\n}\r\n\r\n.time-select-every {\r\n  padding: 10px 56px;\r\n  height: 80px;\r\n}\r\n\r\n.select-date-input {\r\n  margin-top: 10px;\r\n  width: 100%;\r\n  height: 40px;\r\n  outline: none;\r\n  border: 1px solid #ccc;\r\n  font-size: 15px;\r\n  padding-left: 10px;\r\n  border-radius: 5px;\r\n}\r\n\r\n.active-time {\r\n  color: #259b24;\r\n}\r\n\r\n.select-place-btn {\r\n  bottom: 7px;\r\n  width: 96%;\r\n  font-size: 16px;\r\n  background: #259b24;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

