<template>
    <div class="weui-tab soccer-body">
        <div class="weui-tab__panel">
            <div class="container-header">
                <i class="iconfont icon-jiantou-copy-copy-copy head-back" @click="back()"></i>
                <p class="header-title">羽毛球</p>
            </div>
            <div class="current-time-div">
                <p class="current-time">2018/4/16 14:00-15:00</p>
            </div>
            <div class="soccer-place">
                <div class="scoccer-place-item" v-for="(item,index) in places" :key="item.num" v-if="item.stat==='kexue'">
                    <div class="soccer-place-select" @click="selectPlace(index)"></div>
                    <p>{{item.num}}</p>
                </div>
                <div class="scoccer-place-item" v-else-if="item.stat==='disable'">
                    <div class="soccer-place-disable"></div>
                    <p>{{item.num}}</p>
                </div>
                <div class="scoccer-place-item" v-else>
                    <div class="soccer-place-current" @click="cancleSelect(index)"></div>
                    <p>{{item.num}}</p>
                </div>
            </div>
            <div class="place-enter"></div>
            <div class="place-north"><i class="iconfont icon-daohang icon-north"></i> 北</div>
            <div class="place-enter-introduce"><i class="iconfont icon-jiantouxiangshang icon-enter"></i><span>入口</span></div>
            <div class="weui-flex">
                <div class="weui-flex__item">
                    <div class="placeholder">
                        <div class="placeholder-select"></div><span class="select-title">可选</span>
                    </div>
                </div>
                <div class="weui-flex__item">
                    <div class="placeholder">
                        <div class="placeholder-disable"></div><span class="select-title">不可选</span>
                    </div>
                </div>
                <div class="weui-flex__item">
                    <div class="placeholder">
                        <div class="placeholder-current"></div><span class="select-title">已选</span>
                    </div>
                </div>
            </div>
            <div class="place-total-money" v-show="displayMoney">
                <p class="place-total-info">已选场地</p>
                <p class="place-total-num"> 羽毛球{{placeNum}}场地</p>
                <div class="weui-btn weui-btn_primary confirm-order" @click="confirmPay">{{totalMoney}}确认预定</div>
            </div>
            <div v-show="payForPlace">
                <div class="weui-mask" id="iosMask" style="opacity: 1;"></div>
                <div class="weui-actionsheet weui-actionsheet_toggle" id="iosActionsheet">
                    <div class="weui-actionsheet__title">
                        <p class="weui-actionsheet__title-text">确认付款</p>
                    </div>
                    <div class="weui-form-preview">
                        <div class="weui-form-preview__hd">
                            <label class="weui-form-preview__label weui-font">付款金额</label>
                            <em class="weui-form-preview__value">{{totalMoney}}</em>
                        </div>
                        <div class="weui-form-preview__bd">
                            <div class="weui-form-preview__item">
                                <label class="weui-form-preview__label">场地信息</label>
                                <span class="weui-form-preview__value">羽毛球{{placeNum}}场地</span>
                            </div>
                        </div>
                        <div class="weui-cells">
                            <a class="weui-cell weui-cell_access" href="javascript:;">
                                <div class="weui-cell__bd">
                                    <p class="weui-font">付款方式</p>
                                </div>
                                <div class="weui-cell__ft ">一卡通</div>
                            </a>
                        </div>
                        <div class="weui-form-preview__ft">
                             <div class="weui-btn weui-btn_primary confirm-order" @click="redirectPay">立即付款</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                soccerTime:'2018/4/16 14:00-15:00',  // 从后端获取时间
                totalMoney: '',
                placeNum: '',
                payForPlace: false,
                displayMoney: false,
                checkPlace: [],
                places: [{
                        id: 0,
                        stat: "kexue",
                        num: "1号"
                    },
                    {
                        id: 1,
                        stat: "disable",
                        num: "3号"
                    },
                    {
                        id: 3,
                        stat: "disable",
                        num: "5号"
                    },
                    {
                        id: 4,
                        stat: "kexue",
                        num: "7号"
                    },
                    {
                        id: 0,
                        stat: "kexue",
                        num: "9号"
                    },
                    {
                        id: 0,
                        stat: "disable",
                        num: "11号"
                    },
                    {
                        id: 0,
                        stat: "kexue",
                        num: "2号"
                    },
                    {
                        id: 0,
                        stat: "kexue",
                        num: "4号"
                    },
                    {
                        id: 0,
                        stat: "kexue",
                        num: "6号"
                    },
                    {
                        id: 0,
                        stat: "kexue",
                        num: "8号"
                    },
                    {
                        id: 0,
                        stat: "kexue",
                        num: "10号"
                    },
                    {
                        id: 0,
                        stat: "kexue",
                        num: "12号"
                    }
                ]
            };
        },
        created() {
            console.log(window.localStorage.getItem('soccerTime'))
            this.soccerTime = window.localStorage.getItem('soccerTime')
        },
        mounted() {
            console.log(window.localStorage.getItem('soccerTime'))
            this.soccerTime = window.localStorage.getItem('soccerTime')
            window.localStorage.clear()
            console.log(this.$router)
        },
        methods: {
            selectPlace(index) {
                this.places[index].stat = "current"
                this.checkPlace.push(this.places[index].num)
                this.placeNum = this.checkPlace.toString()
                // 此时每个场地价钱是根据当前用户登录的省份进行判断
                let money = 8 * this.checkPlace.length
                this.totalMoney = '¥' + money + ' '
                this.displayMoney = true
            },
            cancleSelect(index) {
                let placeIndex = this.checkPlace.indexOf(this.places[index].num)
                this.checkPlace.splice(placeIndex, 1)
                this.placeNum = this.checkPlace.toString()
                let money = 8 * this.checkPlace.length
                this.totalMoney = '¥' + money + ' '
                this.places[index].stat = "kexue"
    
            },
            confirmPay() {
                this.payForPlace = true
            },
            redirectPay(){
                this.$router.push('/pay_success')
            },
            back() {
                this.$router.back();
            }
        }
    };
</script>

<style>
    .soccer-body {
        background: #f7f7f7;
    }
    
    .current-time-div {
        position: relative;
        top: 50px;
        height: 48px;
        width: 100%;
        background: #ffffff;
    }
    
    .current-time {
        font-size: 16px;
        text-align: center;
        color: #333;
        padding-top: 12px;
    }
    
    .soccer-place {
        position: relative;
        top:20px;
        margin: 0px auto;
        padding: 0px 0px 0px 8px;
    }
    
    .soccer-place-select {
        width: 100%;
        height: 100%;
        background: url("../assets/img/place_space.jpeg");
        background-size: 100% 100%;
        display: inline-block;
    }
    
    .soccer-place-disable {
        width: 100%;
        height: 100%;
        background: url("../assets/img/palce_disable.jpeg");
        background-size: 100% 100%;
        display: inline-block;
    }
    
    .soccer-place-current {
        width: 100%;
        height: 100%;
        background: url("../assets/img/current_place.jpeg");
        background-size: 100% 100%;
        display: inline-block;
    }
    
    .scoccer-place-item {
        margin-top: 40px;
        display: inline-block;
        margin-left: 3px;
        margin-right: 1px;
        width: 54px;
        height: 100px;
        margin-bottom: 10px;
    }
    
    .scoccer-place-item p {
        text-align: center;
        font-size: 14px;
        padding-top: 4px;
        color: #333;
    }
    
    .place-enter {
        margin-top: 40px;
        height: 10px;
        width: 74%;
        margin-left: 16px;
        background: #cfcfcf;
        display: inline-block;
    }
    
    .place-enter-introduce {
        position: relative;
        width: 20%;
        top: -26px;
        font-size: 12px;
        margin: 0px auto;
    }
    
    .icon-enter {
        padding-right: 4px;
        font-weight: 800;
    }
    
    .place-north {
        padding-left: 16px;
        width: 14%;
        color: #333;
        display: inline-block;
    }
    
    .placeholder {
        width: 60%;
        font-size: 12px;
        margin: 0px auto;
    }
    
    .placeholder-select {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 1px solid #dddddd;
        background: #ffffff;
        display: inline-block;
    }
    
    .placeholder-disable {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 1px solid #dddddd;
        background: #f55d54;
        display: inline-block;
    }
    
    .placeholder-current {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 1px solid #dddddd;
        background: #259b24;
        display: inline-block;
    }
    
    .select-title {
        display: inline-block;
        padding-left: 6px;
        line-height: 4px;
        position: relative;
        bottom: 5px;
        color: #666;
    }
    
    .place-total-money {
        height: 18%;
        width: 100%;
        position: absolute;
        bottom: 0px;
        background: #fff;
    }
    
    .place-total-info {
        font-size: 14px;
        color: #666;
        padding: 10px;
    }
    
    .place-total-num {
        font-size: 14px;
        padding: 0px 10px;
    }
    
    .confirm-order {
        width: 96%;
        font-size: 16px;
        margin: 8px;
        background: #259b24;
    }
    
    .weui-actionsheet__title {
        color: #333;
        height: 38px;
    }
    .weui-font{
        font-size:.9em;
        color:#999999;
    }
</style>



