<template>
    <div class="weui-tab">
        <div class="container-header">
            <i class="iconfont icon-jiantou-copy-copy-copy head-back" @click="back()"></i>
            <p class="header-title">选择时间k</p>
        </div>
        <p class="time-info"><i class="iconfont icon-info icon-time-info"></i>请选择开始时间和结束时间，至少一小时</p>
        <div class="time-select-container">
            <div class="time-select-every">
                <p>选择日期</p>
                <input type="text" placeholder="请选择日期" class="select-date-input" @click="getDate" v-model="currentDate">
            </div>
            <div class="time-select-every">
                <p>选择开始时间</p>
                <input type="text" placeholder="请选择日期" class="select-date-input" @click="getStartTime" v-model="startTime">
            </div>
            <div class="time-select-every">
                <p>选择结束时间</p>
                <input type="text" placeholder="请选择日期" class="select-date-input" v-model="endTime"  @click="getEndTime">
            </div>
            <div class="time-select-every">
                <p class="active-time">{{this.checkTime}}</p>
            </div>
        </div>
        <div class="weui-btn weui-btn_primary select-place-btn" @click="selectPlace">去选场地</div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      checkTime: "", // 当前选中的时间
      currentDate: "",
      startTime: "14:00",
      endTime: "15:00",
      year: "",
      mounth: "",
      day: "",
      dateArr: [
        {
          label: "14:00",
          value: 0
        },
        {
          label: "14:30",
          value: 1
        },
        {
          label: "15:00",
          value: 3
        },
        {
          label: "15:30",
          value: 4
        },
        {
          label: "16:00",
          value: 5
        },
        {
          label: "16:30",
          value: 6
        },
        {
          label: "17:00",
          value: 7
        },
        {
          label: "17:30",
          value: 8
        },
        {
          label: "18:00",
          value: 9
        },
        {
          label: "18:30",
          value: 10
        },
        {
          label: "19:00",
          value: 11
        },
        {
          label: "19:30",
          value: 12
        },
        {
          label: "20:00",
          value: 13
        },
        {
          label: "20:30",
          value: 14
        },
        {
          label: "21:00",
          value: 15
        },
        {
          label: "21:30",
          value: 16
        }
      ]
    };
  },
  mounted() {
    console.log(this.$route.params)
    let myDate = new Date();
    this.year = myDate.getFullYear();
    this.mounth = myDate.getMonth() + 1;
    this.day = myDate.getDate();
    this.currentDate = this.year + "/" + this.mounth + "/" + this.day;
    this.checkTime =
      this.currentDate + " " + this.startTime + "-" + this.endTime;
  },
  methods: {
    getDate() {
      weui.datePicker({
        start: new Date(), // 从今天开始
        end: 2030,
        defaultValue: [this.year, this.mounth, this.day],
        onConfirm: result => {
          this.currentDate =
            result[0].value + "/" + result[1].value + "/" + result[2].value;
          this.checkTime =
            this.currentDate + " " + this.startTime + "-" + this.endTime;
        },
        id: "datePicker"
      });
    },
    getStartTime() {
      weui.picker(this.dateArr, {
        className: "custom-classname",
        container: "body",
        defaultValue: [0],
        onConfirm: result => {
          this.startTime = result[0].label;
          this.checkTime =
            this.currentDate + " " + this.startTime + "-" + this.endTime;
        },
        id: "singleLinePicker"
      });
    },
    getEndTime() {
      weui.picker(this.dateArr, {
        className: "custom-classname",
        container: "body",
        defaultValue: [0],
        onConfirm: result => {
          this.endTime = result[0].label;
          this.checkTime =
            this.currentDate + " " + this.startTime + "-" + this.endTime;
        },
        id: "singleLinePicker"
      });
    },
    back() {
      this.$router.back();
    },
    selectPlace(){
      if(this.$route.params.stat === "0") {
        this.$router.push("/soccer")
      }else{
         this.$router.push("/gymnastics")
      }
    }
  }
};
</script>

<style>
.time-info {
  margin-top: 60px;
  font-size: 14px;
  padding: 20px 10px 20px 50px;
  color: #888;
}

.icon-time-info {
  padding-right: 10px;
}

.time-select-container {
  width: 100%;
  height: 74%;
}

.time-select-every {
  padding: 10px 56px;
  height: 80px;
}

.select-date-input {
  margin-top: 10px;
  width: 100%;
  height: 40px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 15px;
  padding-left: 10px;
  border-radius: 5px;
}

.active-time {
  color: #259b24;
}

.select-place-btn {
  bottom: 7px;
  width: 96%;
  font-size: 16px;
  background: #259b24;
}
</style>



